import React from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import Header from "./Header";
import homePage from '../pages/home';
import awardPage from '../pages/award';
import projectPage from '../pages/project';
import referencePage from '../pages/reference';

const MyRouter: React.FunctionComponent = props => {
    return <Router basename="/react_github_page">
        <Header/>
        <Route exact path="/" component={homePage}/>
        <Route exact path="/project" component={projectPage}/>
        <Route exact path="/award" component={awardPage}/>
        <Route exact path="/reference" component={referencePage}/>
    </Router>
}

export default MyRouter;